@use 'fonts';
@use 'default';
@use 'kniiife-data';


.header {
    padding: 1.6rem;
    border-bottom: 2px solid #222;

    &__headline {
        @include fonts.font;
        color: var(--primary-heading-color);
        font-family: fonts.$font-primary;
        margin-bottom: 1.6rem;
    }

    &__subheadline {
        @include fonts.font(36px);
        font-weight:300;
        color: #444;
        margin-top: 0;
    }
}